.tabs-containerTwo {
 display: flex;
 justify-content: flex-start;
 max-width: 100%;
}

.tabs-container {
 max-width: 100%;
 margin: 0 auto;
 text-align: center;
}

.tabs-cont {
 display: inline-block;
}

.tabsTwo {
 list-style: none;
}

.tabsTwo li {
 margin-bottom: 10px;
}

.tabs {
 display: flex;
 list-style: none;
 margin: 0 auto 42px;
}

.tabsTwo li:not(:last-child),
.tabs li:not(:last-child) {
 margin-right: 7px;
}

.tabsTwo li a,
.tabs li a {
 @include font-size(30px);
 display: block;
 position: relative;
 top: 0;
 width: 53px;
 height: 53px;
 //	line-height: 53px;
 color: #fff;
 border-radius: 50%;
 background: #81868e;
 @include transition(all, 0.52s, ease);
 display: flex;
 align-items: center;
 justify-content: center;
 text-decoration: none;
}

.tabsTwo li a {
 text-align: center;
}

.tabs li.active a,
.tabs li a:hover,
.tabsTwo li.active a,
.tabsTwo li a:hover {
 opacity: 1;
 background: $body_aksentcolor;
 top: 0;
}

.tabs-content {
 position: relative;
 z-index: 2;
 padding: 25px;
}

.tabs-panelTwo,
.tabs-panel {
 display: none;
 text-align: left;
}

.tabs-contentTwo {
 padding-top: 0;
}

.tabs-panel_cont {
 column-count: 2;
 column-gap: 110px;
}

.tabs-panel_cont4 p a,
.tabs-panel_cont p a {
 color: $body_aksentcolor;
}

.tabs-panelTwo.active,
.tabs-panel.active {
 display: block;
}

.tabs-panelTwo p+div,
.tabs-panel p+div {
 margin-top: 15px;
}

@media screen and (max-width: 600px) {

 .tabsTwo,
 .tabs {
  flex-direction: column;
 }

 .tabsTwo li,
 .tabs li {
  width: 100%;
 }

 .tabsTwo li:not(:last-child),
 .tabs li:not(:last-child) {
  margin-right: 0;
 }

 .tabsTwo li a,
 .tabs li a {
  border-radius: 0;
  opacity: 1;
  top: 0;
 }

 .tabsTwo li.active a::before .tabs li.active a::before {
  content: "";
  padding-right: 5px;
 }

 .tabs-content {
  border-radius: 0;
 }
}

