 background-size: cover;
 min-height: 140px;
 position: relative;
 display: flex;
 justify-content: center;
 align-items: center;
}

.contact_item_icon {
 width: 42px;
 height: 50px;
 background-position: center center;
 background-repeat: no-repeat;
}

.contact_item_info {
 background-color: #fff;
 padding: 25px 0;
 text-align: center;
}

.contact_info_title {
 color: #464646;
 font-size: 16px;
 font-weight: 400;
 line-height: 20px;
 text-transform: uppercase;
 letter-spacing: 0.64px;
 text-align: center;
 margin-bottom: 10px;
}

.contact_item_list {
 list-style: none;
 @include fontsize-lineheight(20, 14);
 color: #81868e;
 font-size: 14px;
 font-weight: 400;
 line-height: 20px;
 text-transform: uppercase;
 letter-spacing: 0.7px;
}

.contact_form {
display: flex;
flex-wrap: wrap;
justify-content: space-between;
}

.contact_form_field {
width:30%;
height: 36px;
line-height: 36px;
margin-bottom: 25px;
border:1px solid #d8dce0;
text-indent: 10px;
color:#a9a9a9;
 @include transition(all, 0.5s, ease);
}

.contact_form_textarea {
width:100%;
min-height: 190px;
border:1px solid #d8dce0;
color:#a9a9a9;
resize:none;
margin-bottom: 30px;
}
.contact_form_textarea:focus,
.contact_form_textarea:hover,
.contact_form_field:hover,
.contact_form_field:focus {
border:1px solid darken(#d8dce0, 30%);
	}


.contact_form_button {
width: 100%;
text-align: center;
text-transform: uppercase;
padding: 15px 0;
background-color: $body_aksentcolor;
color:#fff;
border:0;
@include transition(all, 0.52s, ease);
cursor: pointer;
}

.contact_form_button:hover {
	background-color: darken($body_aksentcolor, 10%);
}

.checkbox_box {
 margin: 10px auto;
 width: 100%;
 padding: 0;
 color: #fff;
}

.ans-label {
 color: #a9a9a9;
 font-weight: 600;
 padding-top: 7px;
}

.ans-radio {
 color:  #a9a9a9;
 font-weight: normal;
 padding-right: 10px;
 vertical-align: middle;
}

.ans-radio:hover{
border:1px solid darken( #a9a9a9, 30%);
	}

.ans-radio {
 display: flex;
 flex-direction: row;
 align-items: center;
 margin-bottom: 1em;
 padding: 0;
 font-size: 13px;
}

.ans-label {
 display: flex;
 flex-direction: row;
 align-items: flex-start;
 /*margin-bottom: 1em;*/
 padding: 0;
 font-size: 13px;
}

.checkbox_box .ans-label input[type="checkbox"] {
 -webkit-appearance: none;
 -moz-appearance: none;
 appearance: none;
 margin-top: 0;
 margin-bottom: 0;
 margin-right: 0.7em;
 margin-left: 0;
}

.checkbox_box .ans-label input[type="checkbox"]:focus {
 // outline: thin dotted;
}


.checkbox_box .ans-label input[type="checkbox"] {
 position: relative;
 width: 21px;
 height: 20px;
 border: 1px solid  #d8dce0;
 border-radius: 2px;
 background-color: #ffffff;
 margin-bottom: 1em;
}

.checkbox_box .ans-label input[type="checkbox"]::after {
 content: '';
 width: 70%;
 height: 45%;
 border-radius: 0;
 margin: auto;
 position: absolute;
 top: 3px;
 left: 3px;
 -webkit-transition: background 0.25s ease;
 transition: background 0.25s ease;
}

.checkbox_box .ans-label input[type="checkbox"]:checked {
 background-color: darken(#d8dce0, 30%);
}

.checkbox_box .ans-label input[type="checkbox"]:checked::after {
 border-left: 3px solid;
 border-bottom: 3px solid;
 border-color: #ffffff;
 border-radius: 0;
 transform: rotate(-45deg) translate3d(0, 0, 0);
 transform-origin: center center;
}

/* IE 10+ */
@media screen and (-ms-high-contrast: active),
(-ms-high-contrast: none) {
.checkbox_box .ans-label input[type="checkbox"] {
  border: none;
 }

 .checkbox_box .ans-label input[type="checkbox"] {
  background-color: transparent;
 }
}




